.ContactPage {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: stretch;
        align-items: stretch;
    margin: 64px 288px;
}

.ContactPage__title {
    padding: 0 16px;
    font-size: 42px;
}

.logo {
    border-radius: 100%;
    width: 28px;
    padding: 7px;
    font-size: 16px;
}

.twitterLogo {
    background-color: deepskyblue;
}

.mailLogo {
    background-color: crimson;
}

.facebookLogo {
    background-color: mediumblue;
}

.linkedinLogo {
    background-color: darkblue;
}

.githubLogo {
    background-color: black;
}


