.Header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding: 0 128px;
	background: #213B82;
	color: white;
}

.Header__user-info {
	display: -ms-flexbox;
	display: flex; 
	-ms-flex-align: center; 
	    align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.Header__avatar {
	border-radius: 100px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 60px;
	height: 60px;
	margin: 0 12px;
	background-color: #2161B7;
	font-size: 28px;
    font-weight: bold;
}

.Header__logo {
	text-transform: capitalize;
}

.Header__logo a {
	text-decoration: none;
	color: #424242;
}

.Navigation {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
	-ms-flex-align: center;
	    align-items: center;
	list-style: none;
}


