.DateForm {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    vertical-align: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
    width: 160px;
    height: 36px;
    padding: 16px;
    background: #F1F2F3;
    color: #B2B5BA;
    text-transform: uppercase;
}

.DateInput {
    width: 100%;
    padding-top: 4px;
    background: inherit;
    border: none;
    font-size: 20px;
}

.DateInput:focus {
    outline:none;
}


